<template>
  <v-row
    justify="center"
    align="center"
  >
    <h3>{{ category }}</h3>

    <v-col cols="12">
      <v-row class="justify-center">
        <Album v-for="album in getAlbums"
        v-bind:title="album.title"
        v-bind:cover="album.cover"
        v-bind:key="album.title"/>
      </v-row>

    </v-col>
  </v-row>

</template>
<script>
  import Album from "./Album"

  export default {
    name: 'AlbumsType',
    components: {Album},
    computed: {
      getAlbums () {
        return this.$store.getters.getAlbums(this.category)
      }
    },
    props: {
      category: {
        type: String,
        default: "Missing category"
      },
    },
  }
</script>